    height: 6px;
    width: 6px;
    background-color: transparent;
}
.re-doc-nav::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background-color: transparent;
}
.re-doc-nav:hover::-webkit-scrollbar-thumb {
    background-color: rgba(69, 90, 100, 0.2);
}
.re-doc-nav__item a, .re-doc-nav__subitem a {
    margin: 0;
    display: block;
    color: #455a64;
    font-size: 16px;
    padding: 10px calc($re-doc-padding/2) 10px 40px;
    line-height: 24px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.re-doc-nav__item a.active, .re-doc-nav__subitem a.active {
      color: #3498db;
      background-color: #f5f7fa;
}
.re-doc-nav__item > a {
